/*
** Admin / Layout
** ----------------------------------------------------------------------------- */
.chipmunk * {
  box-sizing: border-box;
}
.chipmunk h1:not([class]),
.chipmunk h2:not([class]),
.chipmunk h3:not([class]),
.chipmunk ul:not([class]),
.chipmunk ol:not([class]),
.chipmunk li:not([class]),
.chipmunk table:not([class]) {
  margin: 0;
  padding: 0;
  color: inherit;
}
.chipmunk p {
  margin: 0;
  padding: 0;
  color: inherit;
}
.chipmunk table.form-table:not(:first-child),
.chipmunk p.submit:not(:first-child) {
  margin-top: 1rem;
}
.chipmunk a {
  text-decoration: none;
  outline: none;
  box-shadow: none;
}

.chipmunk__wrap {
  width: 100%;
  padding-right: clamp(1.5em, 2.5vw, 3em);
  padding-left: clamp(1.5em, 2.5vw, 3em);
}

.chipmunk__head {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: var(--chipmunk--color--primary);
  color: var(--chipmunk--color--white);
}
.chipmunk__head h1 {
  display: flex;
  align-items: center;
  gap: 0.75em;
}
.chipmunk__head svg {
  fill: currentColor;
  height: 1em;
  width: 1em;
  font-size: 1.25em;
}

.chipmunk__settings {
  padding-top: 3em;
  padding-bottom: 3em;
}

.chipmunk__head,
.chipmunk__main {
  padding-top: 3em;
  padding-bottom: 3em;
}

.chipmunk__main .notice {
  margin: 0;
}
.chipmunk__main .notice:not(:last-child) {
  margin-bottom: 3em;
}
.chipmunk__main .notice p {
  margin: 0.75em 0;
}
.chipmunk__main h1,
.chipmunk__main h2 {
  line-height: 1.5;
}

/*
** Admin / License
** ----------------------------------------------------------------------------- */
.chipmunk__license {
  display: flex;
  flex-direction: column;
  width: 100%;
  background: #fff;
  border: 1px solid var(--chipmunk--color--gray-light);
  position: relative;
}
@media (min-width: 680px) {
  .chipmunk__license {
    max-width: 420px;
  }
}

.chipmunk__license-head {
  margin: 0;
  padding: 1em;
  width: auto;
  background: var(--chipmunk--color--white);
  border-bottom: 1px solid var(--chipmunk--color--gray-light);
}

.chipmunk__license-body {
  padding: 1em;
}
.chipmunk__license-body input.regular-text {
  width: 100%;
  margin: 0;
}
.chipmunk__license-body input.regular-text:not(:last-child) {
  margin-bottom: 0.5em;
}

.chipmunk__license-data {
  background: var(--chipmunk--color--white);
  padding: 1em;
  border-top: 1px solid var(--chipmunk--color--gray-lighter);
}
.chipmunk__license-data a {
  color: inherit;
  text-decoration: none;
}
.chipmunk__license-data.is-valid {
  color: var(--chipmunk--color--status-success);
}
.chipmunk__license-data.is-expires-soon, .chipmunk__license-data.is-site_inactive, .chipmunk__license-data.is-item_name_mismatch {
  color: var(--chipmunk--color--status-warning);
}
.chipmunk__license-data.is-expired, .chipmunk__license-data.is-error, .chipmunk__license-data.is-missing, .chipmunk__license-data.is-invalid {
  color: var(--chipmunk--color--status-error);
}

/*
** Admin / Nav
** ----------------------------------------------------------------------------- */
.chipmunk__nav {
  padding-top: 1.5em;
  background-color: var(--chipmunk--color--white);
  border-top: 1px solid var(--chipmunk--color--gray-light);
  border-bottom: 1px solid var(--chipmunk--color--gray-light);
}
.chipmunk__nav ul {
  display: flex;
  gap: 2em;
}
.chipmunk__nav a {
  display: block;
  padding: 0.5em 0;
  border-bottom: 3px solid transparent;
  color: var(--chipmunk--color--gray);
  font-size: 1.25em;
  outline: none;
}
.chipmunk__nav a:hover {
  color: var(--chipmunk--color--gray-dark);
}
.chipmunk__nav a.active {
  border-bottom-color: currentColor;
  color: var(--chipmunk--color--black);
}

/*
** Admin / Status
** ----------------------------------------------------------------------------- */
.chipmunk__status {
  display: block;
  margin-top: -1.5em;
  margin-bottom: -1.5em;
  padding: 1em 2em;
  background-color: #fff;
  border: 1px solid #d6d6d6;
  border-radius: 0.25rem;
  color: #333;
}

.toplevel_page_chipmunk #wpcontent {
  padding-left: 0;
}